<template>
  <div id="Software">
    <section class="about-two">
      <div class="container">
        <div class="row">
          <div class="col-xl-6">
            <div
              class="about-two__left wow slideInLeft"
              data-wow-delay="100ms"
              data-wow-duration="2500ms"
            >
              <div class="about-two__right">
                <div class="section-title text-left">
                  <span class="section-title__tagline"
                    >Our Company Structure</span
                  >
                  <h2 class="section-title__title">公司架构</h2>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-6">
            <div class="about-two__img">
              <img src="../assets//img/profile.jpg" alt="" />
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "Software",
  data() {
    return {};
  }
};
</script>
<style scoped>
.about-two {
  position: relative;
  display: block;
  padding: 60px 0 100px;
}

.about-two__left {
  position: relative;
  display: block;
}

.about-two__img {
  position: relative;
  display: block;
}

.about-two__img img {
  width: 100%;
}

.about-two__right {
  position: relative;
  display: block;
}

.about-two__right .section-title {
  margin-bottom: 28px;
}

.about-two__text-1 {
  margin: 0;
  padding-bottom: 10px;
  line-height: 28px;
  text-align: left;
}

.about-two__text-2 {
  font-size: 24px;
  color: var(--thm-primary);
  font-weight: 600;
  margin: 0;
  line-height: 40px;
  padding-bottom: 39px;
}

.about-two__progress {
  display: flex;
  align-items: center;
  line-height: 0;
}

.about-two__progress-wrap {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
}

@media (min-width: 768px) {
  .about-two__progress-wrap {
    grid-template-columns: repeat(2, 290px);
  }
  .team-one__img {
    position: relative;
    display: block;
    overflow: hidden;
    transition: all 500ms ease;
  }
}

.about-two__progress-box {
  position: relative;
  display: inline-block;
}

.about-two__progress-box canvas {
  transform: rotate(90deg);
}

.about-two__progress-box span {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  transform: translateY(-50%) translateX(-50%);
  font-size: 24px;
  font-weight: 500;
  line-height: 1;
  color: var(--thm-black);
}

.about-two__progress h3 {
  margin: 0;
  font-size: 20px;
  line-height: 30px;
  font-weight: 800;
  color: var(--thm-black);
  width: 120px;
  margin-left: 20px;
}
.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
    margin-right: auto;
    margin-left: auto;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1200px;
  }
}
.section-title__tagline {
  display: inline-flex;
  color: #0091c7;
  font-size: 16px;
  align-items: center;
  line-height: 30px;
  margin-bottom: 2px;
  font-weight: 400;
}
.section-title__title {
  margin: 0;
  font-weight: 800;
  color: var(--thm-black);
  font-size: 33px;
  line-height: 50px;
}

.about-two__text-1 {
  margin: 0;
  padding-bottom: 10px;
  line-height: 28px;
  text-align: left;
  color: #877878;
}
</style>



